import * as React from "react"
import AppContent from "./AppContent"
import { HeadFC, Link, graphql } from "gatsby"
import Head_ from "./Head_"
import Giscus from "@giscus/react"

const PostContent = ({ data, pageContext }: any) => {
    const { next, prev } = pageContext;
    const { markdownRemark } = data
    const [theme, setTheme] = React.useState('light')
    const { frontmatter, html } = markdownRemark
    React.useEffect(() => {
        const copyboard = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" fill="currentColor" class="w-4 h-4 text-gray-600"><path fill-rule="evenodd" d="M10.986 3H12a2 2 0 0 1 2 2v8a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h1.014A2.25 2.25 0 0 1 7.25 1h1.5a2.25 2.25 0 0 1 2.236 2ZM9.5 4v-.75a.75.75 0 0 0-.75-.75h-1.5a.75.75 0 0 0-.75.75V4h3Z" clip-rule="evenodd" /></svg>`
        const check = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" fill="currentColor" class="w-5 h-5 text-green-400"><path fill-rule="evenodd" d="M12.416 3.376a.75.75 0 0 1 .208 1.04l-5 7.5a.75.75 0 0 1-1.154.114l-3-3a.75.75 0 0 1 1.06-1.06l2.353 2.353 4.493-6.74a.75.75 0 0 1 1.04-.207Z" clip-rule="evenodd" /></svg>`
        const x= `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" fill="currentColor" class="w-5 h-5 text-red-400"><path d="M5.28 4.22a.75.75 0 0 0-1.06 1.06L6.94 8l-2.72 2.72a.75.75 0 1 0 1.06 1.06L8 9.06l2.72 2.72a.75.75 0 1 0 1.06-1.06L9.06 8l2.72-2.72a.75.75 0 0 0-1.06-1.06L8 6.94 5.28 4.22Z" /></svg>`
        const pmd = document.querySelectorAll('.gatsby-highlight')
        const copy = (text: string, dom: HTMLElement) => {
            navigator.clipboard.writeText(text).then(() => {
                dom.innerHTML = check
            }).catch(() => {
                dom.innerHTML = x
            }).finally(()=>{
                const tm = setTimeout(()=>{
                    dom.innerHTML = copyboard
                    clearTimeout(tm)
                },1000)
            })
        }
        for (let index = 0; index < pmd.length; index++) {
            const pmd_ = pmd[index] as HTMLElement
            pmd_.classList.add('group','relative')
            const dom_ = document.createElement('span')
            dom_.innerHTML = copyboard
            dom_.classList.add('copy-btn')
            dom_.onclick = () => copy(pmd_.innerText, dom_)
            pmd_.appendChild(dom_)
        }
    }, [])
    return (
        <AppContent setTheme={setTheme}>
            <div>
                <div>
                    <div className="mt-4 mb-6 select-none rounded-md md:pl-4 text-gray-900 gap-1 flex flex-col md:items-start items-center pt-2 pb-2">
                        {frontmatter.title && <div className="text-2xl font-semibold dark:text-gray-50">{frontmatter.title}</div> || <div>未知</div>}
                        <div className="text-lg text-gray-700 flex gap-1 dark:text-gray-400 flex-col">
                            {frontmatter.tags && <div className="flex flex-row gap-2 md:text-start text-center">{frontmatter.tags.map((item_: any, i: Number) => <Link key={'postcontent-' + item_.text + '-' + i} className="hover:text-gray-900 dark:hover:text-gray-100 transition-all" to={"/tags/" + item_}>🏷️&nbsp;{item_}</Link>) || <div>未知</div>}</div>}
                            {frontmatter.date && <div className="md:text-start text-center">📅:{frontmatter.date}</div> || <div>未知</div>}
                        </div>
                    </div>
                    <div className="w-[98%] left-0 right-0 m-auto">
                        <div className="break-all posts" dangerouslySetInnerHTML={{ __html: html }}></div>
                    </div>
                </div>
                <div className="flex flex-col gap-1 pt-4 border-b border-t pb-4 border-dashed border-gray-300 dark:border-gray-800 items-start text-start">
                    <div>{prev && <Link className="dark:text-gray-50 font-semibold" to={'/posts/' + prev.frontmatter.id}><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" className="w-5 h-5 inline">
                        <path fillRule="evenodd" d="M17 10a.75.75 0 0 1-.75.75H5.612l4.158 3.96a.75.75 0 1 1-1.04 1.08l-5.5-5.25a.75.75 0 0 1 0-1.08l5.5-5.25a.75.75 0 1 1 1.04 1.08L5.612 9.25H16.25A.75.75 0 0 1 17 10Z" clipRule="evenodd" />
                    </svg>
                        {prev.frontmatter.title}</Link>}</div>
                    <div>{next && <Link className="dark:text-gray-50 font-semibold" to={'/posts/' + next.frontmatter.id}>{next.frontmatter.title}<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" className="w-5 h-5 inline">
                        <path fillRule="evenodd" d="M3 10a.75.75 0 0 1 .75-.75h10.638L10.23 5.29a.75.75 0 1 1 1.04-1.08l5.5 5.25a.75.75 0 0 1 0 1.08l-5.5 5.25a.75.75 0 1 1-1.04-1.08l4.158-3.96H3.75A.75.75 0 0 1 3 10Z" clipRule="evenodd" />
                    </svg>
                    </Link>}</div>
                    <div><Link className="dark:text-gray-50 font-semibold" to="/blog">博客页</Link></div>
                </div>
                <div className="mt-6">
                    <Giscus
                        id="comments"
                        repo="zwtghm/zwtghm.github.io"
                        repoId="R_kgDOJc_FkA"
                        category="Announcements"
                        categoryId="DIC_kwDOJc_FkM4CY2_B"
                        mapping="pathname"
                        reactionsEnabled="1"
                        emitMetadata="0"
                        inputPosition="bottom"
                        theme={theme}
                        lang="zh-CN"
                        loading="lazy"
                    />
                </div>
            </div>
        </AppContent>
    )
}

export const pageQuery = graphql`
query post($id:String) {
    markdownRemark(frontmatter: { id: { eq: $id } }) {
      html
      frontmatter {
        date
        id
        title
        tags
      }
    }
  }
  `

export const Head: HeadFC = ({ data }: any) => {
    const { markdownRemark } = data
    const { frontmatter } = markdownRemark
    return <><title>文章 | {frontmatter.title}</title><Head_ /><script dangerouslySetInnerHTML={{ __html: `if (localStorage.ti == undefined && window.matchMedia('(prefers-color-scheme:dark)').matches || localStorage.ti == 1 && window.matchMedia('(prefers-color-scheme:dark)').matches) {document.documentElement.classList.add('dark')} else if (localStorage.ti==0){document.documentElement.classList.add('dark')}` }}></script></>
}
export default PostContent